import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { withRouter } from "react-router";
import { Icon, NavBar } from "antd-mobile";
import { CovidHomePageRoutePath } from "../lazyPages";

export function DataStatisticPagePure(
    {history}: any
) {
    return (
        <NavBar
            mode="light"
            icon={<Icon type="left"/>}
            onLeftClick={() => history.replace(CovidHomePageRoutePath)}
        >
            数据统计
        </NavBar>
    );
}

export const DataStatisticPage = compose(
    hot(module),
    withRouter,
)(DataStatisticPagePure);

export default DataStatisticPage;
